<template>
  <div>
  <!-- Start of Navigation -->
  <nav>
    <!-- <span>RemoteScreen</span>
    <span class="font-weight-light">Controller</span> -->
    <v-app-bar app>
      <v-app-bar-nav-icon
              @click.stop="drawer = !drawer"
              class="hidden-md-and-up"
      ></v-app-bar-nav-icon>
      <v-toolbar-title class="headline text-uppercase">
        <span>RemoteScreen</span>
        <span class="font-weight-light">Controller</span>
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <span class="font-weight-light">info-age GmbH Basel</span>
    </v-app-bar>
    <!-- End of app toolbar -->

    <!-- Start of mobile side menu -->
    <v-navigation-drawer app v-model="drawer" left>
      <!-- Menu title -->
      <v-toolbar flat>
        <v-list>
          <v-list-item>
            <v-list-item-title class="title">Screens</v-list-item-title>
          </v-list-item>
        </v-list>
      </v-toolbar>
      <v-divider></v-divider>
      <!-- Menu Links -->
      <v-list>
        <v-list-item
                v-for="client in orderedClients"
                v-bind:key="client.InstanceName"
                @click="selectMachine(client.InstanceName)"
                exact
        >
          <v-list-item-action>
            <v-icon>{{ machineIcon }}</v-icon>
          </v-list-item-action>
          <v-list-item-content>{{ client.InstanceName }}</v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>
    <!-- End of mobile side menu -->
  </nav>
  <!-- End of Navigation -->
  <v-content class="ma-3">
    <Machine v-bind:name="currMachine"></Machine>
  </v-content>
  </div>
</template>

<script>
  import axios from 'axios'
  import _ from 'lodash'
  import Machine from './Machine'
  import { mdiMonitor } from '@mdi/js'

export default {
  name: "BRemoteMain",
  components: {Machine},
  data: () => ({
    drawer: true,
    machineIcon: mdiMonitor,
    clients: [],
    currMachine: '',
  }),
  computed: {
    orderedClients: function () {
      return _.orderBy(this.clients, 'InstanceName')
    }
  },
  methods: {
    selectMachine: function (name) {
      // `this` inside methods points to the Vue instance
      this.currMachine = name
    }
  },
  mounted () {
    axios
            .get(this.$controllerBase + '/client')
            .then(response => (this.clients = response.data))
  },
};
</script>
