<template>
    <v-form>
        <v-container>
            <v-row>
                <v-col cols="24" sm="12" md="6">
                    <v-switch v-model="autonav" :label="`Autostart`"></v-switch>
                    <v-text-field
                            label="URL"
                            placeholder="https://"
                            v-model="target"
                    ></v-text-field>
                    <v-text-field
                            label="Status"
                            placeholder="slideshow01"
                            v-model="nextstatus"
                    ></v-text-field>
                </v-col>
                <v-col cols="24" sm="6" md="3">
                    <v-btn @click="sendToDisplay" class="mx-2" fab dark color="blue">
                        <v-icon dark>{{ uploadIcon }}</v-icon>
                    </v-btn>
                </v-col>
            </v-row>
        </v-container>
        <v-snackbar
                v-model="snackbar"
                :timeout="snackbartimeout"
                :color="snackbarcolor"
        >
            {{ snackbartext }}
            <v-btn
                    text
                    @click="snackbarauto = false"
            >
                Close
            </v-btn>
        </v-snackbar>
    </v-form>
</template>

<script>
    //  import _ from 'lodash'
    import axios from 'axios';
    import { mdiUpload } from '@mdi/js';


    export default {
        name: 'UploadURL',
        props: {
            'name': { type: String }
        },
        components: {
        },
        data () {
            return {
                target: '',
                uploadIcon: mdiUpload,
                tab: null,
                nextstatus: 'manual01',
                autonav: false,
                snackbartext: '',
                snackbarcolor: 'blue',
                snackbar: false,
                snackbartimeout: 2000,
            }
        },
        watch: {
        },
        methods: {
            postAutonav: function (url) {
                let me = this
                axios
                    .post(me.$controllerBase + '/kvstore/' + me.name + '/autonav', {
                        url: url,
                        nextstatus: me.nextstatus
                    })
                    .then(response => {
                        me.snackbartext = "autostart set: " + JSON.stringify(response.data)
                        me.snackbarcolor = 'blue'
                        me.snackbartimeout = 2000
                        me.snackbar = true
                    }).catch((error) => {
                    me.snackbartext = "error setting autostart: " + error
                    me.snackbarcolor = 'error'
                    me.snackbartimeout = 4000
                    me.snackbar = true
                })
            },
            postNavigate: function (url) {
                let me = this
                axios
                    .post(this.$controllerBase + '/client/' + me.name + '/navigate', {
                        url: url,
                        nextstatus: me.nextstatus
                    })
                    .then(response => {
                        me.snackbartext = "url sent: " + JSON.stringify(response.data)
                        me.snackbarcolor = 'blue'
                        me.snackbartimeout = 2000
                        me.snackbar = true
                    }).catch((error) => {
                    me.snackbartext = "error sending url: " + error
                    me.snackbarcolor = 'error'
                    me.snackbartimeout = 4000
                    me.snackbar = true
                })
            },
            sendToDisplay: function () {
                if (this.target.includes('://')) {
                    let me = this
                    this.postNavigate(this.target)
                    if (me.autonav) {
                        this.postAutonav(this.target)
                    }

                }
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    div.screenshot {
        vertical-align: bottom;
        width: 820px;
        height: 470px;
        position: relative;
        -moz-box-shadow: 1px 2px 4px rgba(0, 0, 0,0.5);
        -webkit-box-shadow: 1px 2px 4px rgba(0, 0, 0, .5);
        box-shadow: 1px 2px 4px rgba(0, 0, 0, .5);
        padding: 10px;
        background: white;
    }

    /* Make the image fit the box */
    div.screenshot img {
        width: 100%;
        border: 1px solid #8a4419;
        border-style: inset;
    }

    div.screenshot::after {
        content: '';
        position: absolute;
        z-index: -1; /* hide shadow behind image */
        -webkit-box-shadow: 0 15px 20px rgba(0, 0, 0, 0.3);
        -moz-box-shadow: 0 15px 20px rgba(0, 0, 0, 0.3);
        box-shadow: 0 15px 20px rgba(0, 0, 0, 0.3);
        width: 70%;
        left: 15%; /* one half of the remaining 30% */
        height: 100px;
        bottom: 0;
    }
    ul {
        list-style-type: none;
        padding: 0;
    }
    li {
        display: inline-block;
        margin: 0 10px;
    }
    a {
        color: #42b983;
    }
</style>
