<template>
    <v-form>
        <v-container>
            <v-row fluid>
                <v-col cols="24" sm="12" md="6">
                    <v-switch v-model="autonav" :label="`Autostart`"></v-switch>
                    <v-select
                            :items="controllers"
                            v-model="controller"
                            item-text="InstanceName"
                            label="Controller"
                            dense
                            solo
                    ></v-select>
                    <v-select
                            :items="templates"
                            v-model="template"
                            label="Template"
                            dense
                            solo
                    ></v-select>
                    <v-text-field
                            label="Status"
                            placeholder="slideshow01"
                            v-model="nextstatus"
                    ></v-text-field>
                </v-col>
                <v-col align="end" cols="24" sm="6" md="3" style="vertical-align: bottom;">
                    <v-btn @click="sendToDisplay" class="mx-2" fab dark color="blue">
                        <v-icon dark>{{ uploadIcon }}</v-icon>
                    </v-btn>
                </v-col>
            </v-row>
            <v-jsoneditor v-model="targetJson" :plus="false" v-bind:height="'400px'" />
        </v-container>
        <v-snackbar
                v-model="snackbar"
                :timeout="snackbartimeout"
                :color="snackbarcolor"
        >
            {{ snackbartext }}
            <v-btn
                    text
                    @click="snackbar = false"
            >
                Close
            </v-btn>
        </v-snackbar>
    </v-form>
</template>

<script>
    //  import _ from 'lodash'
    import VJsoneditor from 'v-jsoneditor/src/index';
    import axios from 'axios';
    import { mdiUpload } from '@mdi/js';


    export default {
        name: 'UploadURL',
        props: {
            'name': { type: String }
        },
        components: {
            VJsoneditor,
        },
        data () {
            return {
                target: '',
                nextstatus: 'manual01',
                uploadIcon: mdiUpload,
                tab: null,
                controllers: [],
                controller: "",
                templates: [],
                template: "",
                autonav: false,
                snackbartext: '',
                snackbarcolor: 'blue',
                snackbar: false,
                snackbartimeout: 2000,
            }
        },
        watch: {
            name: function () {
            },
            controller: function () {
                axios
                    .get(this.$controllerBase + '/controller/' + this.controller + '/templates')
                    .then(response => {
                        this.templates = response.data
                    })
            }
        },
        mounted () {
            axios
                .get(this.$controllerBase + '/controller')
                .then(response => {
                    this.controllers = response.data
                })
        },
        methods: {
            postAutonav: function (url) {
                let me = this
                axios
                    .post(me.$controllerBase + '/kvstore/' + me.name + '/autonav', {
                        url: url,
                        nextstatus: me.nextstatus
                    })
                    .then(response => {
                        me.snackbartext = "autostart set: " + JSON.stringify(response.data)
                        me.snackbarcolor = 'blue'
                        me.snackbartimeout = 2000
                        me.snackbar = true
                    }).catch((error) => {
                        me.snackbartext = "error setting autostart: " + error
                        me.snackbarcolor = 'error'
                        me.snackbartimeout = 4000
                        me.snackbar = true
                    })
            },
            postNavigate: function (url) {
                let me = this
                axios
                    .post(this.$controllerBase + '/client/' + me.name + '/navigate', {
                        url: url,
                        nextstatus: me.nextstatus
                    })
                    .then(response => {
                        me.snackbartext = "url sent: " + JSON.stringify(response.data)
                        me.snackbarcolor = 'blue'
                        me.snackbartimeout = 2000
                        me.snackbar = true
                    }).catch((error) => {
                    me.snackbartext = "error sending url: " + error
                    me.snackbarcolor = 'error'
                    me.snackbartimeout = 4000
                    me.snackbar = true
                })
            },
            postNavigationFull: function (url) {
                let me = this
                axios
                    .post(me.$controllerBase + '/kvstore/' + me.name + '/' + me.template, me.targetJson)
                    .then( response => {
                        me.snackbartext = "template data sent: " + JSON.stringify(response.data)
                        me.snackbarcolor = 'blue'
                        me.snackbartimeout = 2000
                        me.snackbar = true

                        me.postNavigate(url)

                        if (me.autonav) {
                            me.postAutonav(url)
                        }
                    })
                    .catch(error => {
                        me.snackbarautotext = "error sending template data to kvstore " + me.name + ": " + error
                        me.snackbarurlcolor = 'error'
                        me.snackbartimeout = 4000
                        me.snackbarauto = true
                    })
            },
            sendToDisplay: function () {
                let me = this
                axios
                    .get(me.$controllerBase + '/client/' + this.name + '/addr')
                    .then(response => {
                        let addr = response.data
                        let url = 'https://' + addr + '/' + me.controller + '/templates/' + me.template
                        me.postNavigationFull(url)
                    })
                    .catch(error => {
                        me.snackbarautotext = "error getting https addr of " + me.name + ": " + error
                        me.snackbarurlcolor = 'error'
                        me.snackbartimeout = 4000
                        me.snackbarauto = true
                    })

           }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>
