<template>
    <v-container class="lighten-5">
        <v-row class="mb-6" no-gutters>
            <v-col>
                <div class="screenshot">
                    <img style="max-width: 100%; max-height: 100%;" v-bind:src="screenshot" />
                </div>
            </v-col>
            <v-col>
                <v-btn @click="reloadScreenshot" class="mx-2" fab dark color="blue">
                    <v-icon dark>{{ reloadIcon }}</v-icon>
                </v-btn>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
    import { mdiReload } from '@mdi/js'


    export default {
        name: 'Screenshot',
        props: {
            'name': { type: String }
        },
        data () {
            return {
                screenshot: null,
                reloadIcon: mdiReload,
            }
        },
        watch: {

            name: function () {
                this.reloadScreenshot()
            }
        },
        methods: {
            reloadScreenshot: function () {
                this.screenshot = ""
                var me = this
                var downloadingImage = new Image()
                downloadingImage.onload = function () {
                    me.screenshot = this.src
                    //alert('screenshot: ' + me.screenshot)
                }

                downloadingImage.src = this.$controllerBase + '/' + this.name + '/screenshot/medium?' + new Date().getTime()

            },
        },
        mounted () {
            this.reloadScreenshot()
        },
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    div.screenshot {
        vertical-align: bottom;
        width: 820px;
        height: 470px;
        position: relative;
        -moz-box-shadow: 1px 2px 4px rgba(0, 0, 0,0.5);
        -webkit-box-shadow: 1px 2px 4px rgba(0, 0, 0, .5);
        box-shadow: 1px 2px 4px rgba(0, 0, 0, .5);
        padding: 10px;
        background: white;
    }

    /* Make the image fit the box */
    div.screenshot img {
        width: 100%;
    }

    div.screenshot::after {
        content: '';
        position: absolute;
        z-index: -1; /* hide shadow behind image */
        -webkit-box-shadow: 0 15px 20px rgba(0, 0, 0, 0.3);
        -moz-box-shadow: 0 15px 20px rgba(0, 0, 0, 0.3);
        box-shadow: 0 15px 20px rgba(0, 0, 0, 0.3);
        width: 70%;
        left: 15%; /* one half of the remaining 30% */
        height: 100px;
        bottom: 0;
    }
</style>
