<template>
    <v-container class="grey lighten-5">
        <v-row class="mb-6" no-gutters>
            <v-col>
                <div class="screenshot scroll">
                    <vue-code-highlight fluid style="min-width:100%;">{{ console }}</vue-code-highlight>
                </div>
            </v-col>
            <v-col>
                <v-btn @click="reloadConsole" class="mx-2" fab dark color="blue">
                    <v-icon dark>{{ reloadIcon }}</v-icon>
                </v-btn>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
    import axios from 'axios'
    import { mdiReload } from '@mdi/js'
    import { component as VueCodeHighlight } from 'vue-code-highlight';

    export default {
        name: 'Console',
        components: {
            VueCodeHighlight,
        },
        props: {
            'name': { type: String }
        },
        data () {
            return {
                console: "",
                browserlog: [],
                reloadIcon: mdiReload,
            }
        },
        watch: {
            name: function () {
                this.reloadConsole()
            },
        },
        methods: {
            reloadConsole: function () {
                this.console = ""
                //var me = this
                axios
                    .get(this.$controllerBase + '/client/' + this.name + '/browserlog')
                    .then(response => {
                        this.console = ""
                        if ( response.status != 200 ) {
                            alert( "browserlog: " + response.statusText )
                        }
                        if (response.data != null ) {
                            for (let line of response.data.reverse()) {
                                this.console += line + "\n"
                            }
                        }
                    })
            },
        },
        mounted () {
            this.reloadConsole()
        },
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .scroll {
        overflow-y: auto;
    }

    div.screenshot {
        vertical-align: bottom;
        width: 820px;
        height: 470px;
        position: relative;
        -moz-box-shadow: 1px 2px 4px rgba(0, 0, 0,0.5);
        -webkit-box-shadow: 1px 2px 4px rgba(0, 0, 0, .5);
        box-shadow: 1px 2px 4px rgba(0, 0, 0, .5);
        padding: 10px;
        background: white;
    }

    /* Make the image fit the box */
    div.screenshot img {
        width: 100%;
        border: 1px solid #8a4419;
        border-style: inset;
    }

    div.screenshot::after {
        content: '';
        position: absolute;
        z-index: -1; /* hide shadow behind image */
        -webkit-box-shadow: 0 15px 20px rgba(0, 0, 0, 0.3);
        -moz-box-shadow: 0 15px 20px rgba(0, 0, 0, 0.3);
        box-shadow: 0 15px 20px rgba(0, 0, 0, 0.3);
        width: 70%;
        left: 15%; /* one half of the remaining 30% */
        height: 100px;
        bottom: 0;
    }
</style>
